import PropTypes from "prop-types"
import React from "react"
import MailList from "./maillist"
import { Link, graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"

const Sidebar = props => {
  const data = useStaticQuery(graphql`
    {
      allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
        edges {
          node {
            id
            fields {
              slug
            }
            frontmatter {
              tags
              title
              date
              category
              image {
                childImageSharp {
                  fluid(maxWidth: 250, maxHeight: 130) {
                    ...GatsbyImageSharpFluid
                  }
                }
              }
            }
          }
        }
      }
    }
  `)
  const posts = data.allMarkdownRemark.edges
  return (
    <div>
      <div class="card my-4">
        <h5 class="card-header alert alert-secondary text-dark" role="alert">
          البحث <i class="fas fa-search"></i>
        </h5>
        <div class="card-body">
          <div class="input-group">
            <span class="input-group-btn">
              <button class="btn btn-success btn-sm" type="button">
                إذهب!
              </button>
            </span>
            <input
              type="text"
              class="form-control text-left form-control-sm"
              placeholder="... أبحن عن"
            />
          </div>
        </div>
      </div>

      <div class="card my-4">
        <h5 class="card-header alert alert-secondary text-dark" role="alert">
          موضوعات قد تهمك <i class="fas fa-server"></i>
        </h5>
        <div class="card-body">
          <div class="row">
            <div class="col-md-12">
              {posts.slice(5, 7).map(post => (
                <div key={post.id}>
                  <Link
                    to={post.node.fields.slug}
                    style={{ textDecoration: "none" }}
                  >
                    <Img
                      class="articleimg"
                      fluid={post.node.frontmatter.image.childImageSharp.fluid}
                    />
                    <div class="text-secondary">
                      <b>{post.node.frontmatter.title}</b>
                    </div>
                  </Link>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>

      <div class="card my-4">
        <h5 class="card-header alert alert-secondary" role="alert">
          قائمتنا البريدية <i class="fas fa-envelope-open-text"></i>
        </h5>
        <div class="card-body">
          <MailList />
        </div>
      </div>
    </div>
  )
}

Sidebar.propTypes = {
  siteTitle: PropTypes.string,
}

Sidebar.defaultProps = {
  siteTitle: ``,
}
export default Sidebar
