import React from "react"
import { graphql, Link } from "gatsby"
import Layout from "../components/layout"
import Sidebar from "../components/sidebar"
import MailList from "../components/maillist"
export default class BlogList extends React.Component {
  render() {
    const posts = this.props.data.allMarkdownRemark.edges
    const { currentPage, numPages } = this.props.pageContext
    const isFirst = currentPage === 1
    const isLast = currentPage === numPages
    const prevPage =
      currentPage - 1 === 1 ? "/blog" : `/blog/${(currentPage - 1).toString()}`
    const nextPage = `/blog/${(currentPage + 1).toString()}`

    return (
      <Layout>
        <div class="col-md-4" id="content-desktop">
          <Sidebar />
        </div>
        <div class="col-md-8">
          <br />
          <div class="alert alert-secondary" role="alert">
            <h4 class="my-1">
              جميع المقالات <i class="fa fa-tags fa-sm"></i>
            </h4>
          </div>
          <div class="card-body">
            <ul dir="rtl">
              {posts.map(({ node }) => {
                const title = node.frontmatter.title || node.fields.slug
                return (
                  <Link
                    to={node.fields.slug}
                    class="stretched-link text-secondary"
                    key={node.fields.slug}
                  >
                    <i class="fa fa-tags fa-sm"></i> <b>{title}</b>
                    <hr />
                  </Link>
                )
              })}
            </ul>
            <hr />
            <div>
              <b dir="rtl">
                الصفحات <i class="far fa-file"></i> :{" "}
                {Array.from({ length: numPages }, (_, i) => (
                  <Link
                    key={`pagination-number${i + 1}`}
                    to={`/blog/${i === 0 ? "" : i + 1}`}
                  >
                    <span class="badge badge-warning"> {i + 1} </span>{" "}
                  </Link>
                ))}
              </b>
              <hr />
            </div>
            <center>
              {!isLast && (
                <Link to={nextPage} class="btn btn-secondary btn-sm" rel="next">
                  <i class="fas fa-backward"></i> الصفحة التاليه
                </Link>
              )}
              {"  "}
              {!isFirst && (
                <Link to={prevPage} class="btn btn-secondary btn-sm" rel="prev">
                  الصفحة السابقه <i class="fas fa-forward"></i>
                </Link>
              )}
            </center>
          </div>
          <div id="content-mobile">
            <div class="p-3 mb-2 bg-success text-white">القائمة البريدية</div>
            <MailList />
          </div>
          <hr />
        </div>
      </Layout>
    )
  }
}

export const blogListQuery = graphql`
  query blogListQuery($skip: Int!, $limit: Int!) {
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      limit: $limit
      skip: $skip
    ) {
      edges {
        node {
          fields {
            slug
          }
          excerpt
          frontmatter {
            date(formatString: "DD MMMM, YYYY")
            title
          }
        }
      }
    }
  }
`
